body {
  background-color: #f4f4f9;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

.bookwrapper {
  background-color: #fff;
  border-radius: 8px;
  width: 300px;
  padding: 15px;
  transition: all .3s;
  box-shadow: 0 2px 8px #0000001a;
}

.bookwrapper:hover {
  box-shadow: 0 4px 12px #0003;
}

.bookwrapper p {
  margin: 5px 0;
  font-size: 16px;
}

.bookwrapper p:first-of-type {
  color: #333;
  font-weight: bold;
}

input[type="checkbox"] {
  margin-right: 8px;
}

input[type="checkbox"]:checked {
  background-color: #4caf50;
}

select {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 15px;
  font-size: 14px;
  transition: background-color .3s;
}

button:hover {
  background-color: #45a049;
}

button:active {
  background-color: #388e3c;
}

button.remove {
  background-color: #e53935;
}

button.remove:hover {
  background-color: #d32f2f;
}

button.remove:active {
  background-color: #c62828;
}

.save-btn {
  background-color: #2196f3;
}

.save-btn:hover {
  background-color: #1976d2;
}

.newBookDiv {
  background-color: #fff;
  border-radius: 10px;
  width: 500px;
  max-width: 100%;
  padding: 20px;
  box-shadow: 0 2px 8px #0000001a;
}

input[type="text"], select {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 8px;
  font-size: 14px;
}

input[type="checkbox"] {
  width: auto;
  margin-right: 8px;
}

label {
  color: #333;
  margin-bottom: 5px;
  font-weight: bold;
  display: block;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  width: 100%;
  margin-top: 15px;
  padding: 12px 15px;
  font-size: 16px;
  transition: background-color .3s;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

button[type="submit"]:active {
  background-color: #388e3c;
}

form {
  flex-direction: column;
  display: flex;
}

input[type="text"]:focus, select:focus {
  border: 1px solid #4caf50;
  outline: none;
  box-shadow: 0 0 5px #4caf5066;
}
